.teacher-course {
  height: 100%;
}
.teacher-course .header {
  background-color: white;
  padding: 0;
}
.teacher-course .header .course-name {
  float: left;
  margin-left: 30px;
  font-size: 20px;
  color: #007bff;
  cursor: pointer;
}
.teacher-course .header .menu {
  float: right;
  height: 64px;
  margin-right: 30px;
  line-height: 64px;
}
.teacher-course .header .menu .ant-menu-item {
  font-size: 18px;
}
.teacher-course .sider .ant-menu-item {
  color: black;
  font-size: 14px;
  height: 45px;
  line-height: 45px;
}
.teacher-course .sider .ant-menu-item-selected {
  background-color: #007bff;
  color: white;
}
.teacher-course .content {
  height: 100%;
}
.react-resizable {
  position: relative;
  background-clip: padding-box;
}
.react-resizable-handle {
  position: absolute;
  width: 10px;
  height: 100%;
  bottom: 0;
  right: -5px;
  cursor: col-resize;
  z-index: 1;
}
