.user {
  height: 100%;
}
.user .header {
  padding: 0;
  background-color: #4c4c4c;
  color: white;
  height: 60px;
}
.user .header .logo {
  height: 60px;
  float: left;
  line-height: 60px;
  font-size: 20px;
  margin-left: 30px;
}
.user .header .menu {
  float: right;
  background-color: transparent;
  color: white;
  height: 60px;
  line-height: 60px;
  border-bottom-color: transparent;
}
.user .header .menu .subMenu {
  border-bottom-color: transparent;
}
.user .body {
  height: 100%;
}
.user .body .sider {
  height: 100%;
}
.user .body .sider .ant-menu-item {
  color: black;
  padding-left: 50px !important;
  font-size: 18px;
  height: 45px;
  line-height: 45px;
}
.user .body .sider .ant-menu-item-selected {
  background-color: #007bff;
  color: white;
}
.user .body .sider .ant-menu-item-selected .link {
  color: white;
}
.user .body .content {
  height: 100%;
  background-color: white;
}
.user .body .content .card {
  height: 100%;
}
