.student-course {
  height: 100%;
}
.student-course .header {
  background-color: white;
  padding: 0;
}
.student-course .header .course-name {
  float: left;
  margin-left: 30px;
  font-size: 20px;
  color: #007bff;
  cursor: pointer;
}
.student-course .header .menu {
  float: right;
  height: 64px;
  margin-right: 30px;
  line-height: 64px;
}
.student-course .header .menu .ant-menu-item {
  font-size: 18px;
}
.student-course .sider .ant-menu-item {
  color: black;
  font-size: 14px;
  height: 45px;
  line-height: 45px;
}
.student-course .sider .ant-menu-item-selected {
  background-color: #007bff;
  color: white;
}
.student-course .body {
  height: 100%;
}
