.editor {
  height: 100%;
}
.editor .header {
  background-color: white;
  padding: 0;
}
.editor .header .lib-name {
  float: left;
  margin-left: 30px;
  font-size: 20px;
}
.editor .header .create-button {
  float: right;
  margin-right: 10px;
}
.editor .body {
  height: 100%;
}
.editor .body .sider .ant-menu-item {
  color: black;
  font-size: 14px;
  height: 45px;
  line-height: 45px;
}
.editor .body .sider .ant-menu-item-selected {
  background-color: #007bff;
  color: white;
}
.editor .body .content {
  height: 100%;
}
